
.stickyNav {
  list-style-type: none;
  padding: 0;
  overflow: hidden;
  background-color: #333;
  position: sticky;
  bottom: 0;
  margin: 0;
  z-index: 999;
  
}

.navBarMenu {
  float: right;
}

.navBarMenu a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  margin-bottom: 10px;
}

.navBarMenu a:hover {
  background-image: linear-gradient(
    -225deg,
    #231557 0%,
    #44107a 29%,
    #ff1361 67%,
    #fff800 100%
  );  
  border-radius: 15px;
  animation: textclip 2s linear infinite;
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  display: inline-block;
}
@keyframes textclip {
  to {
    background-position: 200% center;
  }
}
