 /* Style all font awesome icons */
 .fa {
  text-align: center;
  text-decoration: none;
  scale: 160%;
}

/* Add a hover effect if you want */
.fa:hover {
  opacity: 0.7;
}

/* Set a specific color for each brand */
.fa-github, .fa-twitter ,.fa-envelope, .fa-linkedin {
  color: white;
} 
